<script>
import {
  Message
} from 'element-ui'
import KtLineChart from '@/components/LineEchart.vue'
import {
  concat,
  map,
  merge
} from 'lodash'
import {
  updateCrumbs
} from '@/common/crossers.js'
import NoteCard from '@/components/NoteCard.vue'
import Vue from 'vue'

export default {
  components: {
    KtLineChart,
    NoteCard
  },
  mounted: function() {
    this.demo.name = this.demo.shortName = this.$route.params.id
    this.demo.type = +this.$route.query.type || this.demo.type
    updateCrumbs.$emit('update-crumbs', [{
      id: 'assetName',
      name: this.demo.name
    }])
    setTimeout(() => {
      this.demo.rateChartOpt = merge({}, this.demo.rateChartOpt, {
        color: ['#f1b277'],
        legend: {
          data: ['']
        },
        tooltip: {
          formatter: (params, ticket, callback) => {
            if (!params.length) return
            return concat([`<table class="chart-tooltip"><tr><th colspan="2">${params[0].name}</th><tr>`],
              map(params, v => {
                return `<tr class="line">
                        <td class="left"><i class="circle" style="color:${v.color}"></i>${v.seriesName}</td>
                        <td class="right">${Vue.filter('ktPercent')(v.value, 2, 1)}</td>
                      </tr>`
              }), '</table>').join('')
          }
        },
        xAxis: {
          data: ['2017-01', '2017-02', '2017-03', '2017-04', '2017-05']
        },
        series: [{
          name: '',
          type: 'line',
          format: 'percent',
          data: [0.21, 0.32, 0.41, 0.34, 0.25],
          symbolSize: 5,
          symbol: 'circle'
        }]
      })
    }, 100)
  },

  methods: {
    editAsset() {
      this.$router.push({
        name: 'AssetForm',
        params: {
          id: this.demo.name
        }
      })
    },

    deleteAsset() {
      Message.info('Demo ')
    }
  },

  data() {
    return {
      historyDealVisible: false,
      // addNoteFocus: false,
      // noteVisibleCount: 2,
      // noteMoreStep: 5,
      demo: {
        name: '',
        shortName: '',
        type: 0, // 0  1 
        // newNote: '',
        rateChartOpt: {},
        notes: [{
          id: 1,
          editActive: false,
          textEdit: '20170228',
          text: '20170228',
          createdAt: new Date()
        }, {
          id: 2,
          editActive: false,
          textEdit: '',
          text: '',
          createdAt: new Date()
        }, {
          id: 3,
          editActive: false,
          textEdit: '',
          text: '',
          createdAt: new Date()
        }, {
          id: 4,
          editActive: false,
          textEdit: '',
          text: '',
          createdAt: new Date()
        }],
        attachments: [{
          name: ''
        }, {
          name: ''
        }],
        products: [{
          name: 'D2',
          count: 1000,
          amount: 4715000,
          ratio: 0.14,
          todayEarning: 30000,
          totalEarning: 150000
        }, {
          name: 'C2',
          count: 300,
          amount: 3905000,
          ratio: 0.12,
          todayEarning: 28000,
          totalEarning: 168000
        }],
        historyDeals: [{
          dealAt: new Date(),
          name: 'D2',
          opType: '',
          amount: '100020'
        }, {
          dealAt: new Date(),
          name: 'C2',
          opType: '',
          amount: '180623'
        }]
      }
    }
  }
}
</script>

